import { mount } from 'enzyme';
import React from 'react';
import { Button } from './Button';
import { defaultValues } from '../../constants/defaultValues';

export const Component = (
  isDisabled,
  isFullWidth,
  isRound,
  size,
  type,
  value,
  onClick
) => {
  return (
    <Button
      isDisabled={isDisabled}
      isFullWidth={isFullWidth}
      isRound={isRound}
      size={size}
      type={type}
      onClick={onClick}
    >
      {value}
    </Button>
  );
};

let output;

export const tests = (
  button = Component(
    defaultValues.isDisabled,
    defaultValues.isFullWidth,
    defaultValues.isRound,
    defaultValues.size,
    defaultValues.type,
    defaultValues.value,
    defaultValues.onClick
  )
) => {
  output = mount(button);

  return describe('Checkbox', () => {
    it(`Should have isDefaultChecked: ${button.props.value}`, () => {
      expect(output.props().isDefaultChecked).toEqual(button.props.value);
    });
  });
};

tests();
